@import "var";
@import "mixins";

.about {
  margin: 0 auto;
  padding-bottom: 50px;
  background: $light-grey;
}

.about__title {
  margin: 0 0 40px;
  font-family: "Anton", sans-serif;
  font-size: 86px;
  line-height: 126px;
  text-align: center;
  text-transform: uppercase;
}
.about__moto {
  margin-top: 50px;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: $grey;
}

.about__wrapper {
  position: relative;
  width: 202px;
  margin: 0 auto;
  text-align: center;

  &::before {
    position: absolute;
    content: "";
    top: -20px;
    left: -20px;

    width: 109px;
    height: 80px;
    background: $striped-gradient;
  }
  &::after {
    position: absolute;
    content: "";
    bottom: -20px;
    right: -20px;

    width: 109px;
    height: 80px;
    background: $striped-gradient;
  }
  img {
    display: block;
    width: 100%;
    position: relative;
    z-index: 1;
  }
}
.about__social {
  display: none;
  position: absolute;
  bottom: 45px;
  right: 45px;
  .about__link {
    margin-left: 35px;
  }
}

.arrow {
  display: none;
  position: absolute;
  bottom: 45px;
  left: 45px;

  width: 2px;
  height: 23px;

  background: $dark;
  border: none;
  cursor: pointer;
  transition: transform 0.3s linear;

  &::after,
  &::before {
    content: "";
    display: block;
    position: absolute;
    border: 6px solid transparent;
    border-bottom: 0;
  }

  &::after {
    bottom: -5px;
    right: -5px;
    border-top-color: $dark;
  }

  &:hover {
    @include shadow-transform;
  }
}

.button--about {
  margin: 0 auto;
}

@media (min-width: $mobile) {
  .about {
    width: $mobile;
  }
}

@media (min-width: $tablet) {
  .about {
    width: $tablet;
    position: relative;
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;

    padding-bottom: 120px;
  }

  .about__title {
    font-size: 98px;
    line-height: 144px;
  }

  .about__wrapper {
    margin-right: 75px;
    width: 264px;
    &::before,
    &::after {
      width: 141px;
      height: 104px;
    }
    &::before {
      top: -30px;
      left: -30px;
    }
    &::after {
      bottom: -30px;
      right: -30px;
    }
  }

  .about__description {
    margin-left: 130px;
  }

  .arrow {
    display: block;
  }

  .about__social {
    display: block;
  }

  .button--about {
    width: 220px;
  }
}

@media (min-width: $desktop) {
  .about {
    width: $desktop;
    flex-direction: row;

    height: 245px;
  }
  .about__wrapper {
    position: absolute;
    top: -216px;
    right: 10%;
    width: 430px;
    &::before,
    &::after {
      width: 232px;
      height: 175px;
    }
    &::before {
      top: -50px;
      left: -50px;
    }
    &::after {
      bottom: -50px;
      right: -50px;
    }
  }
  .about__description {
    margin-left: 200px;
  }
  .about__title {
    font-size: 128px;
    line-height: 188px;
  }
  .about__moto {
    text-align: left;
  }
  .button--about {
    margin: 0;
  }

  .about__social {
    bottom: 45px;
    right: 45px;
    .about__link {
      display: block;
      margin-top: 35px;

      font-size: 14px;
      line-height: 16px;
    }
  }
}
