@import "var";
@import "mixins";

.catalog__list {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
}

.catalog__item {
  margin-bottom: 30px;
}

.catalog__pic {
  width: 130px;
  img {
    display: block;
    width: 100%;
  }
}

.catalog__title {
  margin: 15px 0 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}
.catalog__price {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  &::before {
    content: "$";
    margin-right: 5px;
  }
}

.catalog__item--top {
  align-self: flex-start;
}

.catalog__all {
  margin-bottom: 67px;
  text-align: center;
  a {
    padding-bottom: 7px;
    border-bottom: 1px solid $dark;
    transition: color 0.3s linear;
    &:hover {
      text-decoration: none;
      border: none;
      color: black;
    }
  }
}

@media (min-width: $mobile) {
}

@media (min-width: $tablet) {
  .catalog__pic {
    width: 324px;
  }
}

@media (min-width: $desktop) {
  .catalog {
    position: relative;
  }
  .catalog__all {
    position: absolute;
    top: 0;
    right: 45px;
    a {
      font-size: 18px;
      line-height: 21px;
    }
  }
  .catalog__pic {
    width: 270px;
  }
  .catalog__item {
    align-self: flex-start;
  }
  .catalog__item--overalls {
    order: 4;
  }
  .catalog__price {
    font-size: 16px;
    line-height: 19px;
  }
  .catalog__title {
    a {
      font-size: 18px;
      line-height: 21px;
    }
  }
}
