@import "var";
@import "mixins";

.section {
  padding: 0 15px;
  box-sizing: border-box;
}

.section-title {
  margin: 60px 0 40px;
  font-weight: bold;
  font-size: 26px;
  line-height: 30px;
  align-items: center;
  color: $dark;
}

@media (min-width: $mobile) {
  .section {
    margin: 0 auto;
    width: $mobile;
  }
}

@media (min-width: $tablet) {
  .section {
    width: 100%;
    padding: 0 45px;
  }
}

@media (min-width: $desktop) {
}
