@import "var";
@import "mixins";

.footer-nav__list {
  @include reset-list;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex-wrap: wrap;
  height: 200px;
}
.footer-nav__item {
  margin-bottom: 30px;
}

@media (min-width: $mobile) {
  .footer-nav__logo {
    padding: 0;
    text-align: left;
  }
}

@media (min-width: $tablet) {
  .footer-nav__list {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .footer-nav__logo {
    padding: 50px 0 0;
    text-align: center;
  }
  .footer-nav__item {
    margin-right: 35px;
    &:first-child {
      width: 100%;
      margin: 0;
    }
  }
}
