.about {
  margin: 0 auto;
  padding-bottom: 50px;
  background: #f1f2f4; }

.about__title {
  margin: 0 0 40px;
  font-family: "Anton", sans-serif;
  font-size: 86px;
  line-height: 126px;
  text-align: center;
  text-transform: uppercase; }

.about__moto {
  margin-top: 50px;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #727a82; }

.about__wrapper {
  position: relative;
  width: 202px;
  margin: 0 auto;
  text-align: center; }
  .about__wrapper::before {
    position: absolute;
    content: "";
    top: -20px;
    left: -20px;
    width: 109px;
    height: 80px;
    background: repeating-linear-gradient(120deg, transparent, transparent 10px, #000, #000 12px); }
  .about__wrapper::after {
    position: absolute;
    content: "";
    bottom: -20px;
    right: -20px;
    width: 109px;
    height: 80px;
    background: repeating-linear-gradient(120deg, transparent, transparent 10px, #000, #000 12px); }
  .about__wrapper img {
    display: block;
    width: 100%;
    position: relative;
    z-index: 1; }

.about__social {
  display: none;
  position: absolute;
  bottom: 45px;
  right: 45px; }
  .about__social .about__link {
    margin-left: 35px; }

.arrow {
  display: none;
  position: absolute;
  bottom: 45px;
  left: 45px;
  width: 2px;
  height: 23px;
  background: #151c22;
  border: none;
  cursor: pointer;
  transition: transform 0.3s linear; }
  .arrow::after, .arrow::before {
    content: "";
    display: block;
    position: absolute;
    border: 6px solid transparent;
    border-bottom: 0; }
  .arrow::after {
    bottom: -5px;
    right: -5px;
    border-top-color: #151c22; }
  .arrow:hover {
    transform: translate(0px, 4px);
    box-shadow: 0px -4px 2px rgba(0, 0, 0, 0.2); }

.button--about {
  margin: 0 auto; }

@media (min-width: 320px) {
  .about {
    width: 320px; } }

@media (min-width: 768px) {
  .about {
    width: 768px;
    position: relative;
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
    padding-bottom: 120px; }
  .about__title {
    font-size: 98px;
    line-height: 144px; }
  .about__wrapper {
    margin-right: 75px;
    width: 264px; }
    .about__wrapper::before, .about__wrapper::after {
      width: 141px;
      height: 104px; }
    .about__wrapper::before {
      top: -30px;
      left: -30px; }
    .about__wrapper::after {
      bottom: -30px;
      right: -30px; }
  .about__description {
    margin-left: 130px; }
  .arrow {
