@media (min-width: 1200px) {
  .about {
    width: 1200px;
    flex-direction: row;
    height: 245px; }
  .about__wrapper {
    position: absolute;
    top: -216px;
    right: 10%;
    width: 430px; }
    .about__wrapper::before, .about__wrapper::after {
      width: 232px;
      height: 175px; }
    .about__wrapper::before {
      top: -50px;
      left: -50px; }
    .about__wrapper::after {
      bottom: -50px;
      right: -50px; }
  .about__description {
    margin-left: 200px; }
  .about__title {
    font-size: 128px;
    line-height: 188px; }
  .about__moto {
