@import "var";

.new {
  display: flex;
  flex-direction: column;
}

.new__wrapper {
  display: flex;
  width: 100%;
  padding: 15px 15px 0 15px;
  box-sizing: border-box;
  background: $striped-gradient;
}

.new__description {
  width: 90%;
  margin: 0 auto;
  padding: 40px 36px;
  background: $light-grey;
}
.new__title {
  text-align: center;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: $grey;
}

.new__mark {
  display: block;

  font-weight: bold;
  font-size: 26px;
  line-height: 30px;
  color: $dark;
}

.new__img {
  display: flex;
  width: 100%;
  img {
    width: 100%;
    object-fit: cover;
  }
}
.button--new {
  margin: 0 auto;
}

@media (min-width: $tablet) {
  .new {
    flex-direction: row-reverse;
    margin: 0 45px;
  }
  .new__wrapper {
    width: 550px;
    padding: 15px 15px 15px 0px;
  }
}

@media (min-width: $desktop) {
  .new__description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    padding: 70px;
  }
  .button--new {
    margin: 0;
  }
  .new__title {
    text-align: left;
  }
  .new__wrapper {
    width: 560px;
  }
}
