@import "var";
@import "mixins";

.header-nav {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: $mobile;
  padding: 30px 15px 60px;
  box-sizing: border-box;

  background: $light-grey;
}

.header-nav__list {
  display: flex;
  @include reset-list;
}

.header-nav__link {
  display: block;
  margin-right: 25px;

  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  transition: all 0.3s linear;
  &:hover {
    text-shadow: -2px -2px 2px rgba(0, 0, 0, 0.2);
    transform: translate(2px, 2px);
    text-decoration: none;
  }
  &:active {
    transform: scale(1.1);
  }
}

@media (min-width: $tablet) {
  .header-nav {
    width: $tablet;
    padding: 45px 45px 60px;
  }
  .header-nav__link {
    margin-right: 35px;
  }
}

@media (min-width: $desktop) {
  .header-nav {
    width: $desktop;
  }
  .header-nav__list {
    flex-direction: column;
  }
  .header-nav__link {
    margin-bottom: 35px;
  }
}
