@import "var";
@import "mixins";

.social__list {
  display: flex;
  @include reset-list;
}

.social__item {
  margin: 0 30px 50px 0;
}

.social__item {
  svg {
    transition: all 0.3s linear;
    &:hover {
      transform: translate(2px, 2px);
      filter: drop-shadow(-2px -2px 2px rgba(255, 255, 255, 0.2));
    }
    &:active {
      transform: scale(1.1);
    }
  }
}

@media (min-width: $mobile) {
}

@media (min-width: $tablet) {
  .social__list {
    justify-content: center;
  }
}

